import dels from '@/static/mbcImg/my/dels.png';
export default {
	data() {
		return {
			bMargin:'1px',
			bBorder:'',
			bBorderStyle:'',
			bLineHeight:'',
			bgImages: this.bgImage,
			names: this.name,
			dels: dels, // 删除图片
		};
	},
	props: [
		'disabled',
		'activeAdd',
		'bColor', 
		'paddingNum', 
		'bgColor', 
		'bShadow', 
		'bgImage',
		'btnData',
		'name',
		'isShowDel',
		'indexNum'],
	components: {},
	computed: {
		i18n() {
			return this.$t('navigation');
		},
	},
	watch: {
		name: {
			handler(a, b) {
				console.log(a, '----------------name--------------')
				this.names = a;
			},
			deep: true
		},
		btnData: {
			handler(a, b) {
				console.log(a, '----------------btnData--------------')
				this.showText(a);
			},
			deep: true
		},
		bgImage: {
			handler(a, b) {
				console.log(a, '----------------bgImage--------------')
				this.bgImages = a;
			},
			deep: true
		},
		activeAdd: {
			handler(a, b) {
				console.log(a, '----------------activeAdd--------------')
				if (a === 0) {
					this.bMargin = '0';
					this.bBorder='1px solid #02C2A2';
					this.bBorderStyle='dashed';
					this.bLineHeight='11.4666vw';
				} else {
					this.bMargin = '1px';
					this.bBorder='';
					this.bBorderStyle='';
					this.bLineHeight='';
				}
			},
			deep: true
		},
		disabled: {
			handler(a, b) {
				console.log(a, '----------------disabled--------------')
				this.showText(this.btnData);
			},
			deep: true
		},
	},
	methods: {
		showText(a){
			console.log(a, '按钮文字显示');
			if (a.type === 0) { // 0 URL  1、立即定制 2、立即购买 3、立即咨询 4、立即询价
				this.names = a.name;
			} else if (a.type === 1) {
				this.names = '立即定制'
			} else if (a.type === 2) {
				this.names = '立即购买'
			} else if (a.type === 3) {
				this.names = '立即咨询'
			} else if (a.type === 4) {
				this.names = '立即询价'
			}
		},
		clickBtn(){
			console.log('用户触发按钮');
			this.$emit('tap-EditAddBtn', this.indexNum);
		},
		clickDelBtn(){
			console.log('点击删除按钮');
			this.bMargin = '1px';
			this.bBorder='';
			this.bBorderStyle='';
			this.bLineHeight='';
			this.$emit('tap-DeleteBtn', this.indexNum);
		}
	}
};
